export default {
    data() {
        return {
            value: '', showKeyboard: true, 
        }
    },
    components: {
        
    },
    beforeCreate(){
        
    },
    computed:{
        
    },
    created(){
        document.body.scrollTop = 0
        document.documentElement.scrollTop = 0
        document.title = '充值'
        
        
    },
    methods:{
        onInput(key) {
            this.value = (this.value + key).slice(0, 14)
        },
        onDelete() {
            this.value = this.value.slice(0, this.value.length - 1)
        },
        next(){
            alert(this.value)
        }
    },
}



